import React from "react";
import ReactDOM from "react-dom";
import * as serviceWorker from "./serviceWorker";
import Home from "./container/Home";
import "./styles/styles.scss";

window.onload = () => {
  document.getElementsByClassName("landing_text_big")[0].style.fontSize =
    "42px";
  document.getElementsByClassName("data_value")[0].innerHTML = "x++";

  let x = 0;
  let t = setInterval(function () {
    document.getElementsByClassName("data_value")[0].innerHTML = x++;
    document.getElementsByClassName("data_value")[1].innerHTML = x++;
    document.getElementsByClassName("data_value")[2].innerHTML = x++;

    if (x > 220) {
      clearInterval(t);
    }
  }, 30);
};

ReactDOM.render(<Home />, document.getElementById("root"));

serviceWorker.unregister();
