import React from "react";
import NavBar from "../components/NavBar";
import Landing from "../components/Landing";
import Features from "../components/Features";
import MenuItems from "../components/MenuItems";
import About from "../components/About";
import Data from "../components/Data";
import Services from "../components/Services";
import Pricing from "../components/Pricing";
import Quotes from "../components/Quotes";
import ContactDet from "../components/ContactDet";
import LoginReg from "../components/LoginReg";
import Contact from "../components/ContactUs";

class Home extends React.Component {
  toggleMenu() {
    this.setState(() => {
      if (this.state.menuItem) return { menuItem: false };
      else return { menuItem: true };
    });
  }
  toggleLogin() {
    this.setState(() => {
      return { loginPage: true };
    });
  }
  toggle_Contact() {
    this.setState(() => {
      return {
        ContactPage: true,
      };
    });
  }
  gotohome() {
    this.setState(() => {
      return {
        loginPage: false,
        ContactPage: false,
      };
    });
  }

  //   user Define function

  constructor(props) {
    super(props);
    this.toggleMenu = this.toggleMenu.bind(this);
    this.toggleLogin = this.toggleLogin.bind(this);
    this.toggle_Contact = this.toggle_Contact.bind(this);
    this.gotohome = this.gotohome.bind(this);

    this.state = {
      menuItem: false,
      loginPage: false,
      ContactPage: false,
    };
  }

  render() {
    return (
      <div>
        {this.state.loginPage ? <LoginReg gotohome={this.gotohome} /> : null}
        {this.state.ContactPage && <Contact gotohome={this.gotohome} />}
        <MenuItems
          isOn={
            this.state.menuItem
              ? { transform: "translateY(0px)" }
              : { transform: "translateY(-200vh)" }
          }
          toggleLogin={this.toggleLogin}
          toggle_Contact={this.toggle_Contact}
        />
        <NavBar
          toggleMenu={this.toggleMenu}
		toggleLogin={this.toggleLogin}
		toggle_Contact={this.toggle_Contact}
          btn={this.state.menuItem ? "cross" : "bar"}
        />
        <Landing />
        <Features />
        <About />
        <Data />
        <Services />
        <Pricing />
        <Quotes />
        <ContactDet />
      </div>
    );
  }
}

export default Home;
